.cc__realvalue {
  font-weight: 500;
  font-size: 28px;
}

.cc__rawvalue__wrapper {
  display: flex;
  font-size: 16px;
  font-weight: 500;
}

  .cc__rawvalue {
    text-decoration: line-through;
  }

  .cc__discount{
    display: flex;
    align-items: center;
    padding-left: 15px;
  }

    .cc__discount__icon > * {
      margin: -5px 1px 0 0;
      fill: #ff853f;
    }

.cc__button__wrapper {
  margin: 15px 0 -5px 0 ;
}

.cc__button {
  width: 100%;
  border: none;
  height: 40px;
  border-radius: 4px;
  font-weight: 500;
  margin: 5px 0;
}

  .cc__addcart:hover{
    background-color: #ff853f;
  }