.cpbr__wrapper {
  padding: 8px 10px;
  border: 1px solid gray;
  border-radius: 6px;
}

.cpbr__wrapper.active{
  background-color: #5972FF;
  color: white;
}

.cpbr__title__wrapper{
  cursor: pointer;
  font-weight: 500;
}

.cpbr__users {
  margin-left: 5px;
  border-spacing: 10px;
}

.cpbr__users > * {
  margin: 0 5px;
}

.cpbr__users.author {
  margin-right: 25px;
}